export default {
    methods: {
      matchUrl(){
        let currentPages = getCurrentPages();
        this.selected = currentPages[currentPages.length-1].route;
		console.log('selected',this.selected)
      }
    },
    data() {
      return {
        //对应mt-tab-item 的id值
        selected: ''
      }
    },
	computed:{
		isDis(){
			if(this.$store.state.login && this.$store.state.login.user && this.$store.state.login.user.member){
				return this.$store.state.login.user.member.isDis
			}
			return ''
		},
	},
    onLoad(){
		console.log('load')
      this.matchUrl();
    },
    onShow () {
		console.log('show')
      this.matchUrl();
      //this.changeMenuActive();
    },
	onReady(){
		console.log('ready')
		this.matchUrl();
	},
    created(){
		console.log('created')
      this.matchUrl();
    }

}
